import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path:"/detail",
      name: 'Detail',
      component: ()=>import('@/components/Detail')
    },
    {
      path:"/order",
      name: 'Order',
      component: ()=>import('@/components/Order')
    },
    {
      path:"/test",
      name: 'test',
      component: ()=>import('@/components/test')
    },
    {
      path:"/me",
      name: 'Me',
      component: ()=>import('@/components/Me')
    },
    {
      path:"/goodsList",
      name: 'GoodsList',
      component: ()=>import('@/components/GoodsList')
      // children:[
      //   {
      //     path:"/goodsList",
      //     name: 'GoodsAll',
      //     component: ()=>import('@/components/goods/GoodsAll')
      //   }]
    },
    {
      path:"/orderList",
      name: 'OrderList',
      component: ()=>import('@/components/OrderList')
    },
    {
      path:"/orderDetail",
      name: 'OrderDetail',
      component: ()=>import('@/components/OrderDetail')
    },
    {
      path:"/logistics",
      name: 'Logistics',
      component: ()=>import('@/components/Logistics')
    },
    {
      path:"/address",
      name: 'Address',
      component: ()=>import('@/components/Address')
    },
    {
      path:"/user",
      name: 'User',
      component: ()=>import('@/components/User')
    },
    {
      path:"/login",
      name: 'Login',
      component: ()=>import('@/components/Login')
    },
    {
      path:"/register",
      name: 'Register',
      component: ()=>import('@/components/Register')
    },
    
  ]
})



