<template>
  <div class="topbar">
    <div class="header"> 
        <p>{{toptitle}}</p>
        <span class="headerfan iconfont" @click='goback'>&#xe654;</span>
      </div>
  </div>
</template>

<script>
export default {
  props:['toptitle'],
  name: 'Topbar',
  data () {
    return {
    }
  },
  methods: {
    goback(){
      this.$router.go(-1);
    }
  }
}
</script>

<style scoped>
    .header{
    width: 100%;
    height: 40px;
    background-color: cornflowerblue;
    text-align: center;
    line-height: 40px;
    color: white;
    font-size: 0.75rem;
    position: fixed;
    top: 0;
    z-index: 1000;
  }
  .headerfan{
    position: absolute;
    top: 0;
    left: 0px;
    margin-left: 10px;
    font-size: 1rem;
  }
  .mint-button--normal {
    float: left;
    background: #26a2ff;
    color: white;
  }
  .order >>> .mint-button-text{
    font-size: 0.75rem;

  }
</style>



