<template>
  <div id="footer">
    <ul class="tabbar">
      <router-link to="/">
        <li :class="[foottab == 1 ? 'iconfont icon-shouye active' : 'iconfont icon-shouye2 ']" ></li>
      </router-link>
      <router-link :to="{name: 'GoodsList'}">
        <li :class="[foottab == 2 ? 'iconfont icon-liebiao2 active' : 'iconfont icon-liebiao3']" ></li>
      </router-link>
      <router-link :to="{name: 'Me'}">
        <li :class="[foottab == 3 ? 'iconfont icon-wodedangxuan active' : 'iconfont icon-wode']" ></li>
      </router-link>
    </ul>
  </div>
</template>

<script>
  export default {
    props:['foottab'],
    data () {
        return {
        }
    }
}
</script>

<style scoped>
  .tabbar{
    background-color: white;
    height: 45px;

    box-shadow: 1px -1px 2px #ddd;
    /* 设置为弹性容器 */
    display: flex;
    /* 换行，第一行在上方 */
    flex-wrap: wrap;
    /* 间隔相等 ，自动分配距离*/
    justify-content: space-around;
  }

  .tabbar li{
    list-style-type: none;
    color: rgb(32, 32, 32);
    height: 100%;
    text-align: center;
    /* line-height: 8rem; */
    line-height: 45px;
    flex-basis: 33%;
    flex-grow: 1;
    font-size: 1.25rem;
  }
  .tabbar a{
    text-decoration: none;
    color: #555;
    line-height: 45px;
    flex-basis: 33%;
    flex-grow: 1;

  }
  .tabbar p{
    font-size: 1rem;
    margin: 0;
  }
  .tabbar span{
    font-size: 1rem;
    line-height: 45px;

  }
  .tabbar .active{
    /* background-color: #f2f2f2; */
    font-size: 1.35rem;
    color: cornflowerblue;

  }
</style>



